.video-card {
    background-color: #ffffff;
    border-radius: 10px;
    box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.1);
    padding: 20px;
    margin: 20px;
    max-width: 300px;
    text-align: center;
  }
  
  .video-title {
    font-size: 1.2rem;
    margin-top: 10px;
  }
  
  .video-player {
    margin-top: 20px;
  }
  